<template>
  <div >
    <div style="font-family: 'Quicksand', sans-serif;margin:0;padding:0;">
          <router-view></router-view>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
    };
  },
  methods: {
    checkDevice(){
      var userAgent = navigator.userAgent || navigator.vendor || window.opera;
      if (/windows phone/i.test(userAgent)) {
          // return "Windows Phone";
          return true;
      }

      if (/android/i.test(userAgent)) {
          // return "Android";
          return true;
      }

      // iOS detection:
      if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
          // return "iOS";
          return true;
      }

      return false;
    }
  },
  mounted() {
    console.log(this.checkDevice())
    if(this.checkDevice()) {
      this.$store.state.mobile=true;
      }else{
        this.$store.state.mobile=false;
    }
  }
};
</script>
<style>
html,body{
  margin:0;
  padding:0;
}
</style>